import React, { useState, useEffect } from 'react';
import ViewHeader from '../shared/components/header/ViewHeader';
import ViewContent from '../shared/components/content/ViewContent';
import { Table, Th, Thead} from 'reactable';
import { useHistory, useParams } from 'react-router-dom';
import {
    Card, Button, Form, FormGroup, Label, Input, Progress, Alert
} from 'reactstrap';
import axios from 'axios';
import './InsertarNota.scss';
import './Archivo.scss';
import IconSearch from 'react-icons/lib/fa/search';
import IconContentCopy from 'react-icons/lib/md/content-copy';

function Archivo(props) {
    const [programas, setProgramas] = useState([]);
    const [error, setError] = useState(false);
    const [programaElegido, setProgramaElegido] = useState('');
    const [fecha, setFecha] = useState(new Date().toISOString().slice(0,10));
    const [notas, setNotas] = useState([]);
    const [loading, setLoading] = useState(false);
    const [progress, setProgress] = useState(0);
    const [ceroNotas, setCeroNotas] = useState(false);
    const axiosConfig = {
        headers: {
            'Authorization': localStorage.getItem('token')
        }
    };

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/api/programa/validos`, axiosConfig)
            .then((response) => {
                setProgramas(response.data.programas);
                setProgramaElegido(response.data.programas[0]._id);
            })
            .catch((error) => {
                setError(true);
            });
    }, []);

    const buscarArchivo = (e) => {
        setLoading(true);
        e.preventDefault();
        axios.get(`${process.env.REACT_APP_API_URL}/api/nota/lista/${programaElegido}/${fecha}`, axiosConfig)
            .then((response) => {
                const notasAInsertar = [...response.data.notas];
                notasAInsertar.map((nota) => {
                    nota.periodista = nota.periodista.usuario;
                    nota.check = <Input type="checkbox" onClick={() => {}} value={nota._id}/>
                });
                setNotas(notasAInsertar);
                setCeroNotas(notasAInsertar.length === 0);
                setTimeout(() => {
                    setLoading(false);
                }, 500)
            })
            .catch((error) => {
                setError(true);
            });
    };

    const getFechaFormateada = (fecha) => {
        const fechaDatos = fecha.split('-');
        return `${fechaDatos[1]}-${fechaDatos[2]}-${fechaDatos[0]}`;
    };

    const onChangePrograma = (e) => {
        setError(false);
        setProgramaElegido(e.target.value);
    };

    const onChangeFecha = (e) => {
        setError(false);
        setFecha(getFechaFormateada(e.target.value));
    };

    const listarProgramas = programas.map((programa) =>  {
        return (
        <option key={programa._id} value={programa._id}>{ programa.nombre }</option>
    )});

    return (
        <div className="view">
            <ViewHeader/>
            <ViewContent>
                <Card className="mb-4">
                    <h2 className="h2">Registro de archivos<small>(historico)</small></h2>
                    { error && <p class="error">Hubo un error al elegir el programa o fecha</p>}
                    <Form onSubmit={ buscarArchivo } className="insertar-nota-form">
                        <FormGroup className="mb-4">
                            <Label>Programa</Label>
                            <Input type="select" name="select" id="exampleSelect" onChange={onChangePrograma}>
                                { listarProgramas }
                            </Input>
                        </FormGroup>
                        <FormGroup className="mb-4">
                            <Label>Fecha</Label>
                            <Input type="date" placeholder="Ingrese una fecha" onChange={ onChangeFecha }/>
                        </FormGroup>
                        <FormGroup className="text-right">
                            <Button color="primary" type="submit"><IconSearch color="#fff" style={{'margin': '0 2px 2px 0'}}/><span>Buscar archivo</span></Button>
                        </FormGroup>
                    </Form>
                    {
                        !loading && notas.length > 0 &&
                        <React.Fragment>
                            <Table className="table" data={notas} sortable={true}>
                                <Thead>
                                    <Th column="check">&nbsp;</Th>
                                    <Th column="numero">No.</Th>
                                    <Th column="id_video"><span>ID VIDEO</span></Th>
                                    <Th column="formato"><span>FORMATO</span></Th>
                                    <Th column="periodista"><span>PERIODISTA</span></Th>
                                    <Th column="carpeta"><span>CARPETA</span></Th>
                                    <Th column="nombre"><span>NOMBRE NOTA</span></Th>
                                    <Th column="area"><span>AREA</span></Th>
                                    <Th column="sello"><span>SELLOS</span></Th>
                                    <Th column="usuario_ultima_modificacion"><span>ULTIMA MODIFICACION</span></Th>
                                </Thead>
                            </Table>
                            <FormGroup className="text-right">
                                <Button color="success"><IconContentCopy color="#fff" style={{'margin': '0 2px 2px 0'}}/><span>Clonar notas</span></Button>
                            </FormGroup>
                        </React.Fragment>
                    }
                    {
                        !loading && ceroNotas &&
                        <Alert color="info">
                            <strong>Lo sentimos!</strong> No se encontraron notas en esa fecha y programa
                         </Alert>
                    }
                    {
                        loading &&   <Progress animated color="info" value={99} />

                    }

                </Card>
            </ViewContent>
        </div>
    );
}

export default Archivo;