import React, { useState, useEffect } from 'react';
import ViewHeader from '../shared/components/header/ViewHeader';
import ViewContent from '../shared/components/content/ViewContent';
import { useHistory } from 'react-router-dom';
import {
    Card, Button, Form, FormGroup, Label, Input, Col
} from 'reactstrap';
import axios from 'axios';
import './InsertarNota.scss';

function InsertarNota(props) {
    const programa = localStorage.getItem('programaElegido');
    const fecha = localStorage.getItem('fecha');
    const [ciudad, setCiudad] = useState('LA PAZ');
    const [formato, setFormato] = useState('NOTA');
    const [nombre, setNombre] = useState('');
    const [area, setArea] = useState('POLITICA');
    const [lead, setLead] = useState('');
    const [fuente, setFuente] = useState('');
    const [vozEnOff, setVozEnOff] = useState('');
    const [localizador, setLocalizador] = useState('');
    const [sello1, setSello1] = useState('');
    const [sello2, setSello2] = useState('');
    const [periodistas, setPeriodistas] = useState([]);
    const [periodista, setPeriodista] = useState(localStorage.getItem('currentPeriodista'));
    const isPeriodista = !!localStorage.getItem('currentPeriodista');
    const history = useHistory();

    const nota = {
        programa,
        fecha,
        ciudad,
        formato,
        nombre,
        area,
        lead,
        fuente,
        localizador,
        sello1,
        sello2,
        voz_en_off: vozEnOff,
        periodista
    };

    const ciudades = [
        'LA PAZ', 'COCHABAMBA', 'SANTA CRUZ', 'SUCRE', 'TARIJA', 'POTOSI', 'ORURO', 'BENI', 'PANDO'
    ];

    const formatos = ['NOTA', 'CUNA', 'VIDEO', 'VO CUNA', 'POSTPRODUCCION', 'FALSO'];

    const areas = ['POLITICA', 'SEGURIDAD', 'JUDICIAL', 'FARANDULA', 'INTERNACIONAL', 'HUMOR'];

    const requiredFields = ['nombre', 'area'];

    const validForm = () => {
        for (let field of requiredFields) {
            if(!nota[field]) {
                document.querySelector(`#${field}`).classList.add('error');
                return false;
            }
        }
        return true;
    }

    const requiredFieldChange = (e) => {
        e.target.classList.remove('error');
    }

    const insertarNota = (e) => {
        e.preventDefault();
        if (validForm()) {
            if (isPeriodista) {
                delete nota.periodista;
            }
            axios.post(`${process.env.REACT_APP_API_URL}/api/nota/crear`, nota, axiosConfig)
            .then(response => {
                history.push('/notas');
            })
            .catch(error => {
                console.log(error);
            })
        }
    };
    
    const axiosConfig = {
        headers: {
            'Authorization': localStorage.getItem('token')
        }
    };

    const onChangePeriodista = (e) => {
        setPeriodista(e.target.value);
    }

    useEffect(() => {
        if (!isPeriodista && periodistas.length === 0) {
            axios.get(`${process.env.REACT_APP_API_URL}/api/usuario/validos`, axiosConfig)
            .then((response) => {
                setPeriodistas(response.data.usuarios.filter(usuario => usuario.role === 'periodista'));
                setPeriodista(response.data.usuarios[0]._id);
            })
            .catch((error) => {
                console.log(error);
            });
        }
    }, [isPeriodista, axiosConfig]);

    return (
        <div className="view">
            <ViewHeader/>
            <ViewContent>
                    <Card className="mb-4 table-responsive">
                        <h6 className="mb-4 text-uppercase">Insertar nota</h6>
                        <Form onSubmit={ insertarNota } className="insertar-nota-form">
                            <FormGroup row>
                                {
                                    !isPeriodista &&
                                    <React.Fragment>
                                        <Label for="periodista" sm={3}>Periodista</Label>
                                        <Col sm={9}>
                                            <Input type="select" name="periodista" id="periodista" placeholder="Periodista" className="text-uppercase" onChange={ onChangePeriodista }>
                                                { periodistas.map((periodista, index) => <option key={periodista._id} value={periodista._id}>{ `${periodista.nombre} ${periodista.apellido}` }</option>)}
                                            </Input>
                                        </Col>
                                    </React.Fragment>
                                }
                                <Label for="ciudad" sm={3}>Ciudad</Label>
                                <Col sm={9}>
                                    <Input type="select" name="ciudad" id="ciudad" placeholder="Ciudad" className="text-uppercase" onChange={ ($event) => setCiudad($event.target.value)}>
                                        { ciudades.map((ciudad, index) => <option key={index}>{ ciudad }</option>)}
                                    </Input>
                                </Col>
                                <Label for="formato" sm={3}>Formato</Label>
                                <Col sm={9}>
                                    <Input type="select" name="formato" id="formato" placeholder="Formato" className="text-uppercase" onChange={ ($event) => setFormato($event.target.value)}>
                                        { formatos.map((formato, index) => <option key={index}>{ formato }</option>)}
                                    </Input>
                                </Col>
                                <Label for="nombre" sm={3}>Nombre de la nota</Label>
                                <Col sm={9}>
                                    <Input type="text" name="nombre" id="nombre" placeholder="Nombre de la nota" className="text-uppercase" onChange={ ($event) => {requiredFieldChange($event);setNombre($event.target.value)}} required/>
                                    
                                </Col>
                                <Label for="area" sm={3}>Area</Label>
                                <Col sm={9}>
                                    <Input type="select" name="area" id="area" className="text-uppercase" onChange={ ($event) => {requiredFieldChange($event);setArea($event.target.value)}}>
                                        { areas.map((area, index) => <option key={index}>{ area }</option>)}
                                    </Input>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Label for="lead" sm={3}>Lead</Label>
                                <Col sm={9}>
                                    <Input type="textarea" name="lead" id="lead" placeholder="Lead" className="text-uppercase" onChange={ ($event) => setLead($event.target.value)}/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Label for="fuente" sm={3}>Fuente</Label>
                                <Col sm={9}
                                    ><Input type="text" name="fuente" id="fuente" placeholder="Fuente" className="text-uppercase" onChange={ ($event) => setFuente($event.target.value)}/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Label for="voz_en_off" sm={3}>Voz en off</Label>
                                <Col sm={9}>
                                    <Input type="textarea" name="voz_en_off" id="voz_en_off" placeholder="Voz en off" className="text-uppercase" onChange={ ($event) => setVozEnOff($event.target.value)}/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Label for="localizador" sm={3}>Localizador</Label>
                                <Col sm={9}>
                                    <Input type="text" name="localizador" id="localizador" placeholder="Localizador" className="text-uppercase" onChange={ ($event) => setLocalizador($event.target.value)}/>
                                </Col>
                            </FormGroup>
                            <FormGroup>    
                                <Label for="sello1" sm={3}>Sello 1</Label>
                                <Col sm={9}>
                                    <Input type="text" name="sello1" id="sello1" placeholder="Sello 1 (max. 50 caracteres)" maxLength="50" className="text-uppercase" onChange={ ($event) => setSello1($event.target.value)}/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Label for="sello2" sm={3}>Sello 2</Label>
                                <Col sm={9}>
                                    <Input type="text" name="sello2" id="sello2" placeholder="Sello 2 (max. 50 caracteres)" maxLength="50" className="text-uppercase" onChange={ ($event) => setSello2($event.target.value)}/>
                                </Col>
                            </FormGroup>
                            <div className="mb-5 botones-insertar-nota">
                                <Button color="warning" className="boton-cancelar" onClick={() => history.push('/notas')}>Cancelar</Button>
                                <Button color="primary" type="submit">Insertar nota</Button>
                            </div>
                        </Form>
                </Card>
            </ViewContent>
        </div>
    );
}

export default InsertarNota;