import React from 'react';
import { BrowserRouter as IndexLink, Link } from "react-router-dom";
import axios from 'axios';
// icons
import IconDashboard from 'react-icons/lib/md/dashboard';
import IconModeEdit from 'react-icons/lib/md/mode-edit';
import IconPictureAsPdf from 'react-icons/lib/md/picture-as-pdf';
import IconSearch from 'react-icons/lib/md/search';
import IconDateRange from 'react-icons/lib/md/date-range';
import IconTextFormat from 'react-icons/lib/md/text-format';
import IconPeople from 'react-icons/lib/md/people';
import IconLocalMovies from 'react-icons/lib/md/list';
import IconGroupAdd from 'react-icons/lib/md/group-add';
import IconLiveTv from 'react-icons/lib/md/live-tv';
import ScrollArea from '../scrollbar';
import logo from '../../../images/cadenaAlogo.png';
import './style.css';


const NavHead = (props) => (
    <header className="nav-head">
        <Link to="/">
            <img src={logo} alt="Cadena A" />
        </Link>
        <div className={`toggle-dot ${props.mini ? 'active': ''}`} onClick={props.toggleNav}></div>
    </header>
);





class NavList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: 0,
            role: localStorage.getItem('role') ? localStorage.getItem('role') : ''
        };
    }
    handleClick = (index, e, link) => {
        let c = e.target.className;
        console.log('******** values ***')
        console.log('index', index)
        console.log('e', e);
        console.log('link', link)
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            selected: (c.indexOf('selected') >= 0) ? '' : index
        });
        if (link){
            this.props.history.push(link);
        }
    }
    handleOpen = (index, e) => {
        e.stopPropagation();
        this.setState({
            selected: index
        })
    }

    generarPauta = (e) => {
        e.preventDefault();
        const programa = localStorage.getItem('programaElegido');
        const fecha = localStorage.getItem('fecha');
        const axiosConfig = {
            headers: {
                'Authorization': localStorage.getItem('token'),
            }
        };
        axios.get(`${process.env.REACT_APP_API_URL}/api/nota/pauta/${programa}/${fecha}`, axiosConfig)
            .then(response => {
                window.open(`${process.env.REACT_APP_API_URL}/${response.data.url}`, 'Download');
            })
            .catch(error => {
                console.warn(error);
            })
    };

    generarReporte = (e) => {
        e.preventDefault();
        const programa = localStorage.getItem('programaElegido');
        const fecha = localStorage.getItem('fecha');
        const axiosConfig = {
            headers: {
                'Authorization': localStorage.getItem('token'),
            }
        };
        axios.get(`${process.env.REACT_APP_API_URL}/api/nota/reporte/${programa}/${fecha}`, axiosConfig)
            .then(response => {
                window.open(`${process.env.REACT_APP_API_URL}/${response.data.url}`, 'Download');
            })
            .catch(error => {
                console.warn(error);
            })
    };

    generarSellos = (e) => {
        e.preventDefault();
        const programa = localStorage.getItem('programaElegido');
        const fecha = localStorage.getItem('fecha');
        const axiosConfig = {
            headers: {
                'Authorization': localStorage.getItem('token'),
            }
        };
        axios.get(`${process.env.REACT_APP_API_URL}/api/nota/sellos/${programa}/${fecha}`, axiosConfig)
            .then(response => {
                window.open(`${process.env.REACT_APP_API_URL}/${response.data.url}`, 'Download');
            })
            .catch(error => {
                console.warn(error);
            })
    };
    render() {
        return <ScrollArea className="nav-list-container" horizontal={false} verticalScrollbarStyle={{width: '4px', marginLeft: '10px'}}>
            <ul className="list-unstyled nav-list clearfix">
                <li><div className="nav-list-title">Notas</div></li>
                <li className={(this.state.selected === 0) ? 'selected': ''}>
                    <Link to="/notas" activeClassName="active">
                        <IconDashboard size="18" color="#509DDE" className="icon-plus-one"/>
                        <span className="name">Dashboard</span>
                    </Link>
                </li>
                <li className={(this.state.selected === 1) ? 'selected': ''}>
                    <Link to="/insertar-nota" activeClassName="active">
                    <IconModeEdit size="18" color="#1EC759" className="icon-mode-edit"/>
                        <span className="name">Insertar nota</span>
                    </Link>
                </li>
                {
                    this.state.role !== 'periodista' && 
                    <>
                        <li><div className="nav-list-title">Reportes</div></li>
                        <li onClick={ this.generarPauta } className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link activeClassName="active">
                                <IconPictureAsPdf size="18" color="#509DDE"/>
                                <span className="name">Generar Pauta</span>
                            </Link>
                        </li>
                        <li onClick={this.generarReporte} className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link activeClassName="active">
                                <IconPictureAsPdf size="18" color="#1EC759"/>
                                <span className="name">Generar Reporte</span>
                            </Link>
                        </li>
                        <li onClick={ this.generarSellos } className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link activeClassName="active">
                                <IconTextFormat size="18" color="#509DDE"/>
                                <span className="name">Generar Sellos</span>
                            </Link>
                        </li>
                    </>
                }                        
                <li><div className="nav-list-title">Historico</div></li>
                <li className={(this.state.selected === 2) ? 'selected': ''}>
                    <Link to="/archivo" activeClassName="active">
                        <IconDateRange size="18" color="#1EC759"/>
                        <span className="name">Archivo</span>
                    </Link>
                </li>
                <li onClick={this.handleClick.bind(this, 2)} className={(this.state.selected === 2) ? 'selected': ''}>
                    <Link to="/busqueda-avanzada" activeClassName="active">
                        <IconSearch size="18" color="#509DDE"/>
                        <span className="name">Busqueda Avanzada</span>
                    </Link>
                </li>
                { this.state.role === 'admin' &&
                    <>
                        <li><div className="nav-list-title">Administracion de programas</div></li>
                        <li onClick={this.handleClick.bind(this, 2)} className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link to="/widgets" activeClassName="active">
                                <IconLocalMovies size="18" color="#509DDE"/>
                                <span className="name">Lista Programas</span>
                            </Link>
                        </li>
                        <li onClick={this.handleClick.bind(this, 2)} className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link to="/widgets" activeClassName="active">
                                <IconLiveTv size="18" color="#1EC759"/>
                                <span className="name">Insertar programa</span>
                            </Link>
                        </li>
                        <li><div className="nav-list-title">Administracion de usuarios</div></li>
                        <li onClick={this.handleClick.bind(this, 2)} className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link to="/widgets" activeClassName="active">
                                <IconPeople size="18" color="#509DDE"/>
                                <span className="name">Lista Usuarios</span>
                            </Link>
                        </li>
                        <li onClick={this.handleClick.bind(this, 2)} className={(this.state.selected === 2) ? 'selected': ''}>
                            <Link to="/widgets" activeClassName="active">
                                <IconGroupAdd size="18" color="#1EC759"/>
                                <span className="name">Insertar usuario</span>
                            </Link>
                        </li>
                    </>
                }
            </ul>
            {/* end scroll-area */}
        </ScrollArea>
    }
}




export default (props) => (
    <nav className={`site-nav ${props.mini ? 'mini' : ''}`}>
        <NavHead {...props}/>
        <NavList/>

    </nav>
);
