import React, { useState, useEffect } from 'react';
import Nav from '../shared/components/nav';
import SiteHead from '../shared/components/header';
import InsertarNota from '../components/InsertarNota';
import ModificarNota from '../components/ModificarNota';
import Notas from '../components/Notas';
import Archivo from '../components/Archivo';
import BusquedaAvanzada from './BusquedaAvanzada';

const Dashboard = (props) => {
        const [navMini, setNavMini] = useState(false);
    
        const toggleNav = (e) => {
            e.preventDefault();
            setNavMini(!navMini);
        };

        const hideNav = (e) => {
            e.stopPropagation();
            e.preventDefault();
            setNavMini(false);
        };

        return (
            <div className="app-wrapper">
                <Nav mini={navMini} toggleNav={toggleNav}/>
                <div className={`content-container ${navMini ? 'full' : ''}`}>
                    <div className="menu-dropshadow" style={{display: (navMini ? 'block': 'none')}} onClick={hideNav}></div>
                    <SiteHead toggleNav={toggleNav}/>
                    { props.location.pathname === '/insertar-nota' && <InsertarNota />}
                    { props.location.pathname.indexOf('/modificar-nota') > -1 && <ModificarNota />}
                    { props.location.pathname === '/notas' && <Notas />}
                    { props.location.pathname === '/archivo' && <Archivo />}
                    { props.location.pathname === '/busqueda-avanzada' && <BusquedaAvanzada />}
                </div>
            </div>
        )
};

export default Dashboard;