import React from 'react';
const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', timezone: 'UTC-4' };

const ViewHeader = () => (
    <div className="view-header">
        <header className="title text-white">
            <h1 className="h4 text-uppercase">{localStorage.getItem('programaElegidoNombre')}</h1>
            <p className="mb-0">{ new Date(localStorage.getItem('fecha')).toLocaleString('es-MX', options) }</p>
        </header>
    </div>
);

export default ViewHeader;