import React, { useState, useEffect } from 'react';
import ViewHeader from '../shared/components/header/ViewHeader';
import ViewContent from '../shared/components/content/ViewContent';
import { useHistory } from 'react-router-dom';
import {
    Card, Popover, PopoverHeader, PopoverBody, Row, Input
} from 'reactstrap';
import { Table, Th, Thead} from 'reactable';
import axios from 'axios';
import socketIOClient from 'socket.io-client';
import confirm from "reactstrap-confirm";
import './Notas.scss';
import IconPeopleOutline from 'react-icons/lib/md/people-outline';
import IconEdit from 'react-icons/lib/fa/edit';
import IconTrash from 'react-icons/lib/fa/trash';

function Notas(props) {
    const [notas, setNotas] = useState([]);
    const [notasError, setNotasError] = useState(false);
    const programa = localStorage.getItem('programaElegido');
    const fecha = localStorage.getItem('fecha');
    const [leadOpen, setLeadOpen] = useState({});
    const [eliminarClonarDisabled, setEliminarClonarDisabled] = useState('disabled');
    const [editarDisabled, setEditarDisabled] = useState('disabled');
    const [noticiasSeleccionadas, setNoticiasSeleccionadas] = useState({
        counter: 0
    });
    const axiosConfig = {
        headers: {
            'Authorization': localStorage.getItem('token')
        }
    };

    const checkboxClicked = (e) => {
        console.log(e.target.checked);
        console.log(e.target.value);
        if (e.target.checked) {
            noticiasSeleccionadas[e.target.value] = true;
            noticiasSeleccionadas.counter += 1
        } else {
            delete noticiasSeleccionadas[e.target.value];
            noticiasSeleccionadas.counter -= 1;
        }
        setEliminarClonarDisabled(noticiasSeleccionadas.counter === 0 ? 'disabled' : '');
        setEditarDisabled(noticiasSeleccionadas.counter === 0 || noticiasSeleccionadas.counter > 1 ? 'disabled' : '');
        setNoticiasSeleccionadas(noticiasSeleccionadas);
    };
    const history = useHistory();
    const modificarNota = () => {
        if (editarDisabled === 'disabled') {
            return;
        }
        let notaId = '';
        for (let key in noticiasSeleccionadas) {
            if (key !== 'counter') {
                notaId = key;
                break;
            }
        }
        history.push(`modificar-nota/${notaId}`)
    };

    const eliminarNotas = async () => {
        if (eliminarClonarDisabled === 'disabled') {
            return;
        }
        const seguro = await confirm({
            title: (
                <>
                    <strong>Cuidado!</strong>
                </>
            ),
            message: `Esta seguro de eliminar estas ${noticiasSeleccionadas.counter} notas?`,
            confirmText: "Si estoy seguro",
            confirmColor: "primary",
            cancelColor: "warning",
            cancelText: 'Cancelar'
        });

        if(seguro) {    
            for (let key in noticiasSeleccionadas) {
                if (key === 'counter') {
                    continue;
                }
                axios.delete(`${process.env.REACT_APP_API_URL}/api/nota/borrar/${key}`, axiosConfig)
                    .then(response => {
                        delete noticiasSeleccionadas[key];
                        noticiasSeleccionadas.counter -= 1;
                        if (checkIfRemoveIsComplete()) {
                            setEliminarClonarDisabled('disabled');
                            setEditarDisabled('disabled');
                            renderNotas();
                        }
                    })
                    .catch(error => {
                        alert(`HUBO UN ERROR AL BORRAR LA NOTA ${error}`)
                    })
            }
        }
    };

    const checkIfRemoveIsComplete = () => {
        return noticiasSeleccionadas.counter === 0;
    }

    const togglePopOver = (e, id) => {
        e.preventDefault();
        e.stopPropagation();
        const nuevoLeadOpen = {...leadOpen};
        if (!nuevoLeadOpen[`Popover-${id}`]) {
            for (let key in nuevoLeadOpen) {
                nuevoLeadOpen[key] = false;
            }
        }
        nuevoLeadOpen[`Popover-${id}`] = !nuevoLeadOpen[`Popover-${id}`];
        setLeadOpen(nuevoLeadOpen);
    };
    const renderNotas = () => {
        axios.get(`${process.env.REACT_APP_API_URL}/api/nota/lista/${programa}/${fecha}`, axiosConfig)
            .then((response) => {
                const notasAInsertar = [...response.data.notas];
                notasAInsertar.map((nota) => {
                    nota.periodista = nota.periodista.usuario;
                    nota.sello = nota.sello ? 'SI' : 'NO';
                    nota.revisado = nota.revisado ? 'SI' : 'NO';
                    nota.nombre = (<React.Fragment>
                                        <span className="nombre-nota" id={`Popover-${nota._id}`} onClick={ ($event) => togglePopOver($event, nota._id) } key={`Popover-${nota._id}`}>
                                            {nota.nombre}
                                        </span>
                                        <Popover placement={'bottom'} isOpen={ !!leadOpen[`Popover-${nota._id}`] } target={`Popover-${nota._id}`} toggle={ ($event) => togglePopOver($event, nota._id) }>
                                            <PopoverHeader>Lead</PopoverHeader>
                                            <PopoverBody>{ nota.lead }</PopoverBody>
                                        </Popover>
                                    </React.Fragment>);
                    nota.check = <Input type="checkbox" onClick={checkboxClicked} value={nota._id}/>
                    nota.videoOnLine = <iframe width="360" height="200" src="https://www.youtube.com/embed/4aMvNmPscF0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"></iframe>
                });
                setNotas(notasAInsertar);
            })
            .catch((error) => {
                console.log('this is the error', error)
                setNotasError(true);
            });
    };

    useEffect(() => {
        const socket = socketIOClient(`${process.env.REACT_APP_API_URL}`);
        socket.on('notasActualizadas', () => {
            renderNotas();
        });
        renderNotas();
        return () => socket.disconnect();
    }, []);
    
    return (
        <div className="view">
            <ViewHeader/>
            <ViewContent>
                <Card className="mb-4 table-responsive">
                    
                        <h6 className="mb-4 text-uppercase">Reporte de notas</h6>
                        <Row className="mb-4 fila-botones-accion">
                <div className="col-sm-3">
                    <Card className={`d-flex p-3 align-items-center accion ${eliminarClonarDisabled}`} onClick={ eliminarNotas }>
                            <div className="action-icon mr-4 bg-danger d-flex align-items-center justify-content-center">
                                <IconTrash size="20" color="#fff"/>
                            </div>
                            <div>
                                <h5>Eliminar</h5>
                                <div>Seleccione una o varias notas para eliminar</div>
                            </div>
                    </Card>
                </div>

                <div className="col-sm-3">
                    <Card className={`d-flex p-3 align-items-center accion ${eliminarClonarDisabled}`}>
                            <div className="action-icon mr-4 bg-success d-flex align-items-center justify-content-center">
                                <IconPeopleOutline size="20" color="#fff"/>
                            </div>
                            <div>
                                <h5>Clonar</h5>
                                <div>Seleccione una nota para clonar</div>
                            </div>
                    </Card>
                </div>

                <div className="col-sm-3">
                    <Card className={`d-flex p-3 align-items-center accion ${editarDisabled}`} onClick={ modificarNota }>
                            <div className="action-icon mr-4 bg-warning d-flex align-items-center justify-content-center">
                                <IconEdit size="20" color="#fff"/>
                            </div>
                            <div>
                                <h5>Modificar</h5>
                                <div>Seleccione una nota para modificar</div>
                            </div>
                    </Card>
                </div>
            </Row>
                        {
                            notasError &&
                                <p className="notas-error">Hubo un problema al obtener las notas</p>
                        }
                        {
                            true &&
                                <Table className="table" data={notas} sortable={true}>
                                    <Thead>
                                        <Th column="check">&nbsp;</Th>
                                        <Th column="numero">No.</Th>
                                        <Th column="id_video"><span>ID VIDEO</span></Th>
                                        <Th column="formato"><span>FORMATO</span></Th>
                                        <Th column="periodista"><span>PERIODISTA</span></Th>
                                        <Th column="carpeta"><span>CARPETA</span></Th>
                                        <Th column="nombre"><span>NOMBRE NOTA</span></Th>
                                        <Th column="area"><span>AREA</span></Th>
                                        <Th column="revisado"><span>REVISADO</span></Th>
                                        <Th column="sello"><span>SELLOS</span></Th>
                                        <Th column="usuario_ultima_modificacion"><span>ULTIMA MODIFICACION</span></Th>
                                        <Th column="videoOnLine">Video On Line</Th>
                                    </Thead>
                                </Table>
                        }
                </Card>
            </ViewContent>
        </div>
    );
}

export default Notas;