import React, { useState, useEffect } from 'react';
import { Link } from 'react-router';
import {
    Form, Input, Label, FormGroup, Button, Card
} from 'reactstrap';
import axios from 'axios';
import '../style.scss';
import logo from '../images/cadenaAlogo.png';

const ElegirPrograma = (props) => {
    const [programasAbierto, setProgramasAbierto ] = useState(false);
    const [errorElegirPrograma, setErrorElegirPrograma] = useState(false);
    const [error, setError] = useState('');
    const [programas, setProgramas] = useState([]);
    const [programaElegido, setProgramaElegido] = useState('');
    const [programaElegidoNombre, setProgramaElegidoNombre] = useState('');
    const [fecha, setFecha] = useState(new Date().toISOString().slice(0,10));

    const axiosConfig = {
        headers: {
            'Authorization': localStorage.getItem('token')
        }
    };

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/api/programa/validos`, axiosConfig)
            .then((response) => {
                setProgramas(response.data.programas);
                setProgramaElegido(response.data.programas[0]._id);
                setProgramaElegidoNombre(response.data.programas[0].nombre);
            })
            .catch((error) => {
                setErrorElegirPrograma(true);
                setError(error);
            });
    }, []);

    const getFechaFormateada = (fecha) => {
        const fechaDatos = fecha.split('-');
        return `${fechaDatos[1]}-${fechaDatos[2]}-${fechaDatos[0]}`;
    };

    const onChangePrograma = (e) => {
        setErrorElegirPrograma(false);
        setProgramaElegido(e.target.value);
        setProgramaElegidoNombre(e.target.options[e.target.options.selectedIndex].innerText);
        console.log(programaElegidoNombre);
    };

    const onChangeFecha = (e) => {
        setErrorElegirPrograma(false);
        setFecha(getFechaFormateada(e.target.value));
    };

    const onElegirProgramaSubmit = (e) => {
        e.preventDefault();
        localStorage.setItem('programaElegido', programaElegido);
        localStorage.setItem('programaElegidoNombre', programaElegidoNombre);
        localStorage.setItem('fecha', fecha);
        props.history.push('/notas');
    };

    const listarProgramas = programas.map((programa) =>  {
        return (
        <option key={programa._id} value={programa._id}>{ programa.nombre }</option>
    )});

    return (
        <div className="view">
        <div className="view-content view-pages view-session d-flex justify-content-center align-items-center flex-column">
            <Card className="mb-3 form-card">
                    <header className="mb-5 login">
                        <Link to="/">
                            <img src={logo} alt="Cadena A" />
                        </Link>
                        <p className="lead">Elige el programa y la fecha:</p>
                    </header>
                    { errorElegirPrograma && <p className="error-login">Hubo un error al elegir el programa { error }</p> }
                    <Form onSubmit={ onElegirProgramaSubmit }>
                        <FormGroup className="mb-4">
                            <Label>Programa</Label>
                            <Input type="select" name="select" id="exampleSelect" onChange={onChangePrograma}>
                                { listarProgramas }
                            </Input>
                        </FormGroup>
                        <FormGroup className="mb-4">
                            <Label>Fecha</Label>
                            <Input type="date" placeholder="Ingrese una fecha" onChange={ onChangeFecha }/>
                        </FormGroup>
                        <FormGroup className="text-right">
                            <Button color="primary" type="submit">Continuar</Button>
                        </FormGroup>
                    </Form>
            </Card>
        </div>
    </div>
    );
};

export default ElegirPrograma;