import React, { useState } from 'react';
import { Link } from 'react-router';
import {
    Form, Input, Label, FormGroup, Button, Card
} from 'reactstrap';
import axios from 'axios';
import './Login.scss';
import '../style.scss';
import logo from '../images/cadenaAlogo.png';

export default (props) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errorLogin, setErrorLogin] = useState(false);

    const login = (e) => {
        e.preventDefault();
        axios.post(`${process.env.REACT_APP_API_URL}/api/usuario/login`, {
            email,
            password
        })
        .then((response) => {
            localStorage.setItem('token', response.data.token);
            localStorage.setItem('role', response.data.role);
            if (response.data.role === 'periodista') {
                localStorage.setItem('currentPeriodista', true);
            }
            props.history.push('/elegir-programa')
        })
        .catch(() => setErrorLogin(true))
    };

    const onChangeUser = (e) => {
        setErrorLogin(false);
        setEmail(e.target.value)
    };

    const onChangePassword = (e) => {
        setErrorLogin(false);
        setPassword(e.target.value)
    };

    return (
        <div className="view">
            <div className="view-content view-pages view-session d-flex justify-content-center align-items-center flex-column">
                <Card className="mb-3 form-card">
                    
                        <header className="mb-5 login">
                            <Link to="/">
                                <img src={logo} alt="Cadena A" />
                            </Link>
                            <p className="lead">Inicio de Sesion</p>
                        </header>
                        <Form onSubmit={login}>
                            { errorLogin && <p className="error-login">El usuario o la contrase&ntilde;a son incorrectos</p> }
                            <FormGroup className="mb-4">
                                <Label>Usuario</Label>
                                <Input type="email" placeholder="Ingrese su usuario" onChange={ onChangeUser }/>
                            </FormGroup>
                            <FormGroup className="mb-4">
                                <Label>Contrase&ntilde;a</Label>
                                <Input type="password" placeholder="Ingrese su contrase&ntilde;a" onChange={ onChangePassword }/>
                            </FormGroup>
                            <FormGroup className="text-right">
                                <Button color="primary" type="submit">Iniciar sesion</Button>
                            </FormGroup>
                        </Form>
                </Card>
            </div>
        </div>
    )
}
