import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Login from './components/Login';
import Dashboard from './components/Dashboard';
import ElegirPrograma from './components/ElegirPrograma';
import './App.scss';

function App() {
  document.title = 'Cadena A - Sistema de notas'
  return (
    <div className="App">
      <Router>
        <Switch>
        <Route exact path="/notas" component={Dashboard}/>
          <Route exact path="/insertar-nota" component={Dashboard}/>
          <Route exact path="/archivo" component={Dashboard} />
          <Route exact path="/busqueda-avanzada" component={Dashboard} />
          <Route exact path="/modificar-nota/:id" component={Dashboard}/>
          <Route exact path="/elegir-programa" component={ElegirPrograma} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/" component={Login} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
